/* DEFINITION DES VARIABLES */
:root {
  --primary-color:#12455A;
  --accent-color: #f7a223;
  --light-color: white;
  --light-blue: #7BC2F8;
  --dark: black;
  --divider-color:#dedede;

  --general-font:"Lexend-Regular";
  --accent-font:"Lexend-Bold";
  --thin-font:"Lexend-Light";
}



